<template>
  <div class="news">
    <van-loading v-if="newdata == ''" class="loading" type="spinner" color="#aa8c5d">正在刷新数据</van-loading>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text="下拉可以刷新" loosing-text="松开立即刷新"
      loading-text="正在刷新数据">
      <!-- <p>刷新次数: {{ count }}</p> -->
      <NewsList v-show="newdata" v-for="news in newdata" :key="news.news_id" :news="news" />
    </van-pull-refresh>
  </div>
</template>

<script>
// import { mapState } from "vuex";
import NewsList from "@/components/NewsList.vue";
// import qs from "qs";
import Vue from 'vue';
import { Toast } from 'vant'
import { Loading } from 'vant';
import { PullRefresh } from 'vant';
Vue.use(Toast);
Vue.use(PullRefresh);
Vue.use(Loading);
export default {
  components: {
    NewsList,
  },
  data: function () {
    return {
      newdata: [],
      // count: 0,
      isLoading: false,
    }
  },
  computed: {
    // ...mapState(["count"]),
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        Toast('刷新成功');
        this.isLoading = false;
        // this.count++;
      }, 1000);
    },
  },
  created() {
    this.axios.post("https://sonydpj.ccwonline.com/getnewslist").then((res) => {
      // console.log(res.data);
      this.newdata = res.data.news_list
    });
  },
};
</script>

<style lang="scss" scoped>
.news {
  width: 100vw;
  height: 100%;
  background: #2e2e31;

  .loading {
    width: 100vw;
    height: 100vh;
    padding-top: 15px;
    text-align: center;
  }

}
</style>